import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import contractABI from "../contracts/Renkon.json";
import Layout from "../layouts/main";
import {
  Button,
  Box,
  TextInput,
  ResponsiveContext,
  Grid,
  Card,
  Stack,
  CardBody,
  Image,
  CardHeader,
  Heading,
  Text
} from "grommet";
const contractAddress = "0x5C0B013DA0CC711afC590AfA13aAe57a029680BB";
import { Renkon } from "../../gen/types/Renkon";


const getWindowEth = () => window !== undefined && window.ethereum;

const getWhitelistProof = (address: string, type: string) => {
  return fetch(`/whitelist/${type}/${address}.json`)
    .then((e) => e.json())
    .then((e) => {
      return e.proof;
    });
};

const requestConnectWallet = async () => {
  const eth = getWindowEth();
  if (eth) {
    try {
      const provider = new ethers.providers.Web3Provider(eth);
      await provider.send("eth_requestAccounts", []);
      return await provider.getSigner();
    } catch (e) {
      console.log(e);
    }
  }
};

const marketingMint = async (
  contract: any,
  proof: any,
  quantity: number,
  price = 25000000,
  walletAddress: any
) => {
  console.log(proof)
  console.log(quantity)
  console.log(walletAddress)
  return contract.publicSaleMint(quantity, { from: walletAddress, value: price * quantity });
};

const callContract = async (contract: any, method: any, param: any) => {
  return await contract[method](param);
};

// markup
const IndexPage = () => {
  const size = React.useContext(ResponsiveContext);
  const [contract, setContract] = useState<Renkon | null>(null);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [proof, setProof] = useState<string | null>(null);
  const [mintQuantity, setMintQuantity] = useState<number>(1);
  const [myNFT, setMyNFT] = useState<[]>([]);
  const [nftMeta, setNftMeta] = useState<[]>([]);
  const [message, setMessage] = useState<string>("");
  const getMyNFT = async (address: string) => {
    return await callContract(contract, "tokensOf", address);
  };

  const getMetaFile = async (tokenId: number) => {
    const url = await callContract(contract, "tokenURI", tokenId);
    return fetch(url).then((e) => e.json());
  };

  const initializeContract = async () => {
    const signer = await requestConnectWallet();
    if (signer) {
      signer.getAddress().then((address) => {
        setWalletAddress(address.toLowerCase());
      });

      const contractObject = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      ) as Renkon;
      setContract(contractObject);
    }
  };

  useEffect(() => {
    if (walletAddress) {
      getWhitelistProof(walletAddress, "fcfs")
        .then((e) => {
          if (e) {
            setProof(e);
            
            contract?.isWhitelisted(e, { from:  walletAddress })
            .then(e => {
              console.log(e)
            })
            
          } else {
            setMessage("You are not whitelisted");
          }
        })
        .catch((e) => {
          setMessage("You are not whitelisted");
        });
      getMyNFT(walletAddress).then((e) => {
        console.log(e);
        setMyNFT(e);
      });
    }
  }, [walletAddress]);

  useEffect(() => {
    Promise.all(
      myNFT.map((nftTokenId) => {
        return getMetaFile(nftTokenId);
      })
    ).then((allMeta: any) => {
      setNftMeta(allMeta);
    });
  }, [myNFT]);

  return (
    <Layout>
      <ResponsiveContext.Consumer>
        {(size) => (
          <>
            <Box
              gap="small"
              direction="column"
              width={size === "small" ? "100%" : "60%"}
            >
              <h6>contract: {contractAddress}</h6>
              {walletAddress && (
                <>
                  <Button
                    color="accent-1"
                    primary
                    label={`Login as ${walletAddress}`}
                  />
                </>
              )}
              {!walletAddress && (
                <Button
                  label="Connect Wallet"
                  onClick={initializeContract}
                  primary
                ></Button>
              )}

              {contract && proof && (
                <>
                              <TextInput
                placeholder="Quantity"
                value={mintQuantity}
                onChange={(event) =>
                  setMintQuantity(Number(event.target.value))
                }
              />
                  <Button
                    label="Mint"
                    onClick={() => {
                      marketingMint(contract, proof, mintQuantity, 35000000, walletAddress)
                      .catch(e => {
                        setMessage('Minting not successful')
                      });
                    }}
                    primary
                  ></Button>
                </>
              )}
              {message}
              <Grid columns={size !== "small" ? "small" : "100%"} gap="small">
                {[...nftMeta].map((card: any, index) => (
                  <Card key={index}>
                    <Stack anchor="bottom-left">
                      <CardBody>
                        <Image fit="contain" src={card.image} />
                      </CardBody>

                      <CardHeader
                        pad={{ horizontal: "small", vertical: "small" }}
                        // https://gist.github.com/lopspower/03fb1cc0ac9f32ef38f4#all-hex-value-from-100-to-0-alpha
                        background="#000000A0"
                        width="medium"
                        justify="start"
                      >
                        <Box>
                          {" "}
                          <Heading level="5" margin="none">
                            {card.name}
                          </Heading>
                         
                        </Box>
                      </CardHeader>
                    </Stack>
                  </Card>
                ))}
              </Grid>
              
            </Box>
          </>
        )}
      </ResponsiveContext.Consumer>
    </Layout>
  );
};

export default IndexPage;
